import React from 'react';
type Point = {
  x: number;
  y: number;
  time: number;
};

function distance(p1: Point, p2: Point) {
  return Math.hypot(p2.x - p1.x, p2.y - p1.y);
}

function midpoint(p1: Point, p2: Point) {
  return { x: (p1.x + p2.x) / 2, y: (p1.y + p2.y) / 2, time: 0 };
}

function gradient(a: Point, b: Point) {
  return (b.y - a.y) / (b.x - a.x);
}

export function Game() {
  const [particles, setParticles] = React.useState<Array<Point>>([]);
  const canvasRef = React.useRef<HTMLCanvasElement>(null);
  const [ctx, setCtx] = React.useState<CanvasRenderingContext2D>();

  React.useEffect(() => {
    if (ctx) {
      if (particles.length > 2) {
        const p1 = particles[particles.length - 1];
        const p2 = particles[particles.length - 2];
        const speed = distance(p1, p2) / (p1.time - p2.time);
        const f = speed / 10;
        const t = (Math.abs(p1.x - p2.x) / window.innerWidth) * 200;

        ctx.beginPath();
        ctx.moveTo(particles[0].x, particles[0].y);

        var m = 0;
        var dx1 = 0;
        var dy1 = 0;

        var preP = particles[0];

        for (let i = 1; i < particles.length - 1; i++) {
          var curP = particles[i];
          const nexP = particles[i + 1];
          m = gradient(preP, nexP);
          const dx2 = (nexP.x - curP.x) * -f;
          const dy2 = dx2 * m * t;

          ctx.bezierCurveTo(
            preP.x - dx1,
            preP.y - dy1,
            curP.x + dx2,
            curP.y + dy2,
            curP.x,
            curP.y
          );

          dx1 = dx2;
          dy1 = dy2;
          preP = curP;
        }
        ctx.stroke();
      }
    }
  }, [ctx, particles]);

  React.useEffect(() => {
    const interval = setInterval(() => {
      ctx?.fillRect(0, 0, window.innerWidth, window.innerHeight);
    }, 50);

    return () => {
      clearInterval(interval);
    };
  }, [ctx]);
  React.useEffect(() => {
    if (canvasRef.current) {
      canvasRef.current.width = window.innerWidth;
      canvasRef.current.height = window.innerHeight;

      const ctx = canvasRef?.current?.getContext('2d');
      if (ctx) {
        ctx.strokeStyle = 'rgb(17, 94, 50)';
        ctx.fillStyle = `hsl(56, 70%, 0%, 0.1)`;

        setCtx(ctx);
      }
    }
  }, [canvasRef]);

  React.useEffect(() => {
    const handler = (event: MouseEvent) => {
      const newParticles = particles.slice(0);
      newParticles.push({
        x: event.clientX,
        y: event.clientY,
        time: Date.now(),
      });
      if (newParticles.length > 80) {
        newParticles.shift();
      }
      setParticles(newParticles);
    };
    window.addEventListener('mousemove', handler);
    return () => {
      window.removeEventListener('mousemove', handler);
    };
  }, [particles]);
  return <canvas className="ball" ref={canvasRef}></canvas>;
}
