import React from 'react';
import './App.css';
import { Popover, Typography } from '@mui/material';

export function Icons({ children, text }) {
  const [popover, setPopover] = React.useState(null);
  const open = Boolean(popover);
  return (
    <div>
      <Typography
        ariaOwns={open ? 'mouse-over-popover' : undefined}
        ariaHaspopup="true"
        onMouseEnter={(e) => setPopover(e.currentTarget)}
        onMouseLeave={(e) => setPopover(null)}
      >
        {children}
      </Typography>
      <Popover
        id="mouse-over-popover"
        sx={{
          pointerEvents: 'none',
        }}
        open={open}
        anchorEl={popover}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
        onClose={(e) => setPopover(null)}
        disableRestoreFocus
      >
        <Typography sx={{ p: 1 }}>{text}</Typography>
      </Popover>
    </div>
  );
}
